<template>
  <div>
        <!-- 头部 -->
    <nav-bar class="home-nav">
      <div slot="left" class="left" @click="$router.go(-1)">
        <img src="~assets/img/common/back.svg" />
      </div>
      <div slot="center" class="header">
        <span>添加地址</span>
      </div>
    </nav-bar>
    <van-address-edit class="address"
      :area-list="areaList"
      show-set-default
      show-search-result
      :search-result="searchResult"
      :area-columns-placeholder="['请选择', '请选择', '请选择']"
      @save="onSave"
      @change-detail="onChangeDetail"
    >
    </van-address-edit>
  </div>
</template>

<script>
import areaList from 'assets/js/area'
import NavBar from "components/common/navbar/NavBar";
export default {
  name: "Address",
  data() {
    return {
      areaList,
      addressInfo:{},
      searchResult: [],
      addAddress:{},
      uid:localStorage.getItem('uid'),
    };
  },
  components: {
    NavBar
  },
  created() {},
  mounted() {
    console.log(this.addressInfo);
    console.log(this.uid);
  },
  methods: {
 async onSave(data) {
      console.log(data);
      this.addressInfo = {
        province:data.province,
        city:data.city,
        area:data.county,
        address:data.addressDetail,
        connect_name:data.name,
        connect_phone:data.tel,
        default:data.isDefault?1:0,
        type:"1",
        uid:localStorage.getItem('uid'),
        areaCode:data.areaCode,
      }
     let res =await  this.$http.post(this.$api.ADDRESS,this.addressInfo)
         console.log(res);
         console.log(this.addressInfo.id);
         if (res.data.code===200){
           this.$toast('添加成功');
           setTimeout(()=>{
             this.$router.go(-1)
           },500)
         }  
    },
    onChangeDetail(val) {
      if (val) {
        this.searchResult = [
          {
            name: "阳光广场中心",
            address: "山西创业大厦"
          }
        ];
      } else {
        this.searchResult = [];
      }
    },
  }
};
</script>
<style lang='scss' scoped>
.nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  background-color: #fff;
}
.left {
  padding-top: 5px;
  padding-left: 5px;
}
.address{
  margin-top: 1.9rem;
}
</style>
